body {
    background-color: #FAFAFF;
}

.hero-component {
    margin-top: 55px;
}

.head-1 {
    font-weight: 700;
    font-size: 48px;
}

.para-1 {
    font-size: 20px;
    margin-top: 32px;
}

.action-buttuon {
    background-color: #5555FF;
    color: #ffff;
}

.action-buttuon:hover {
    color: #ffff;
}

.hero_image {
    height: 325px;
    text-align: center;
    justify-content: center;
    align-items: center;
    width: 100%;
}

.brand-logo {
    height: auto;
    width: auto;
}

.branding {
    width: 100%;
    text-align: center;
    justify-content: center;
    align-items: center;
}

.brand-head {
    font-size: 32px;
    font-weight: 600;
    margin-left: 25px;
}

.card {
    width: auto;
    margin-top: 12px;
}

.card-btn {
    width: 100%;
    background-color: #5555FF;
    color: #ffff;
    text-decoration: none;
}

.card-btn:hover {
    color: #ffff;
    text-decoration: none;
}

.badge {
    position: absolute;
    background-color: #FCAF3B;
    margin-top: 5px;
    margin-left: 5px;
    color: black;
}

span {
    font-size: 25px;
    font-weight: 400;
}

.rate-list .rate-list-link {
    list-style-type: none;
    margin-left: -35px;
    font-size: 16px;
    line-height: 28px;
}

.card-btn-odd {
    width: 100%;
    background-color: #FAFAFF;
    color: black;
    text-decoration: none;
}

.prod-rate-details {
    margin-top: 55px;
}

.middle-card {
    background-color: #5555FF;
    color: white;
}

.testimonial {
    margin-top: 70px;
    width: 665px;
    margin: auto;
}

.testi-head {
    font-size: 50px;
}

.testi-image {
    text-align: center;
    align-items: center;
    justify-content: center;
    width: 100%;
    margin-bottom: 60px;
}

.head-2 {
    font-size: 40px;
}

.para-2 {
    font-size: 18px;
}

.caption {
    text-align: center;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin-top: 60px;
}

.vector-image {
    background-color: #E0E0FF;
    padding: 15px;
    border-radius: 50%;
}
.contact-section{
    width: 790px;
    margin: auto;
    margin-top: 60px;
    margin-bottom: 60px;
}
.contact {
    background-color: #242464;  
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-end;
    padding: 42px 64px 64px;
    border-radius: 20px;
    margin-top: 25px;
}
form{
    color: white;
}
.form-button{
    float: right;
    background-color: #5555FF;
    color: #ffff;
}
.form-button:hover{
    color: #ffff;

}
@media (max-width: 750px) {
    .brand-head {
        text-align: center;
    }

    .testimonial {
        width: auto;
        text-align: center;
    }

    .testi-head {
        font-size: 40px;
    }
    .contact-section{
        width: auto;
       padding: 10px;
    }
    .form-button{
        width: 100%;
    }
    
}